import Vue from 'vue'
import Router from 'vue-router'
import IndexView from '@/components/IndexView'
import ArchivesView from '@/components/ArchivesView'
import OencvDay1 from '@/article/OencvDay1'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/blog'
    },
    {
      path: '/blog',
      name: 'Index',
      component: IndexView
    },
    {
      path: '/archives',
      name: 'archives',
      component: ArchivesView
    },
    {
      path: '/OencvDay1',
      name: 'OencvDay1',
      component: OencvDay1
    }
  ],
  mode: 'history'
})
